<template>
    <div class="bk-exception bk-exception-center" v-show="show">
        <img :src="image">
        <template v-if="$slots.message">
            <slot name="message" />
        </template>
        <template v-else>
            <h2 class="exception-text">{{ message }}</h2>
        </template>
    </div>
</template>

<script>
/**
     * @desc  app-exception
     *  @desc 
     *  @param type {String} - 404403500building
     *  @param delay {Number} - 
     *  @param text {String} - 404403Sorry500building()
     *  @example1 <app-exception type="404"></app-exception>
     */

    export default {
        name: 'AppException',
        props: {
            type: {
                type: String,
                default: '404',
            },
            delay: {
                type: Number,
                default: 0,
            },
            text: {
                type: String,
                default: '',
            },
        },
        data () {
            let message = '';
            let image = '';

            switch (this.type) {
                case '403':
                    image = '/static/images/403.png';
                    message = 'Sorry';
                    break;

                case '404':
                    image = '/static/images/404.png';
                    message = '';
                    break;

                case '500':
                    image = '/static/images/500.png';
                    message = '!';
                    break;

                case 'building':
                    image = '/static/images/building.png';
                    message = '';
                    break;
            }

            if (this.text) {
                message = this.text;
            }

            return {
                show: false,
                message,
                image,
            };
        },
        created () {
            setTimeout(() => {
                this.show = true;
            }, this.delay);
        },
    };
</script>
