<template>
    <jb-collapse-item name="host">
        <span class="panel-title">
            <span>{{ ''}}<span class="strong number">{{ data.length }}</span>{{ ''}}</span>
            <span v-if="statisticsData.fail"><span class="error number">{{ statisticsData.fail }}</span>{{ 'Agent' }}</span>
        </span>
        <action-extend :list="list" copyable :invalid-list="invalidList">
            <template v-if="editable">
                <div class="action-item" @click="handleRemoveAll">{{ '' }}</div>
                <div class="action-item" @click="handleRemoveFail">{{ '' }}</div>
            </template>
        </action-extend>
        <template #content>
            <div v-bkloading="{ isLoading }">
                <host-table
                    v-if="!isRequestError"
                    :editable="editable"
                    :list="list"
                    :max-height="410"
                    :append-nums="invalidList.length"
                    :diff="diff"
                    @on-change="handleRemoveOne">
                    <tbody
                        v-if="invalidList.length > 0"
                        class="invalid-list"
                        slot="appendBefore">
                        <tr v-for="(row) in invalidList" :key="row.realId">
                            <td class="table-cell">
                                <span
                                    class="invalid"
                                    v-bk-tooltips="''">
                                    {{ '' }}
                                </span>
                                <span>{{ row.ip }}</span>
                            </td>
                            <td>--</td>
                            <td>--</td>
                            <td>--</td>
                            <td>--</td>
                            <td v-if="editable" class="action-column">
                                <bk-button text @click="handleInvalidRemove(row)">{{ '' }}</bk-button>
                            </td>
                        </tr>
                    </tbody>
                </host-table>
                <bk-exception v-if="isRequestError" type="500" style="padding-bottom: 50px;">
                    <div style="display: flex; font-size: 14px;">
                        <span></span>
                        <bk-button text @click="handleRefresh"></bk-button>
                    </div>
                </bk-exception>
            </div>
        </template>
    </jb-collapse-item>
</template>
<script>
    import _ from 'lodash';
       import AppManageService from '@service/app-manage';
    import JbCollapseItem from '@components/jb-collapse-item';
    import ActionExtend from '../components/action-extend';
    import HostTable from '../components/host-table';
    import {
        sortHost,
        statisticsHost,
        generateHostRealId,
    } from '../components/utils';

    export default {
        name: 'ViewHost',
        components: {
            JbCollapseItem,
            ActionExtend,
            HostTable,
        },
        props: {
            data: {
                type: Array,
                required: true,
            },
            editable: {
                type: Boolean,
                default: false,
            },
            allPanel: {
                type: Boolean,
                default: false,
            },
            diff: {
                type: Object,
                default: () => ({}),
            },
        },
        data () {
            return {
                isLoading: false,
                isRequestError: false,
                list: [{},{},{}],
                invalidList: [{},{},{}], // 
            };
        },
        computed: {
            statisticsData () {
                return statisticsHost(this.list);
            },
        },
        watch: {
            data: {
                handler (data) {
                    if (this.isInnerChange) {
                        this.isInnerChange = false;
                        return;
                    }
                    if (data.length < 1) {
                        this.list = [];
                        return;
                    }
                    this.fetchHostOfHost();
                },
                immediate: true,
            },
        },
        created () {
            this.isInnerChange = false;
        },
        methods: {
            /**
             * @desc ip
             */
            fetchHostOfHost () {
                this.isLoading = true;
                const ipList = [];
                const ipMap = {};

                // 
                this.data.forEach((currentHost) => {
                    const realId = generateHostRealId(currentHost);
                    ipList.push(realId);
                    ipMap[realId] = Object.assign({ realId }, currentHost);
                });
                AppManageService.fetchHostOfHost({
                    ipList,
                })
                    .then((data) => {
                        const list = [];
                        this.invalidList = [];
                        // list
                        data.forEach((currentHost) => {
                            const realId = generateHostRealId(currentHost);
                            if (ipMap[realId]) {
                                list.push(Object.assign({ realId }, currentHost));
                                delete ipMap[realId];
                            }
                        });
                        this.list = Object.freeze(sortHost(list));
                        // delete
                        this.invalidList = Object.freeze(Object.values(ipMap));
                        this.isRequestError = false;
                    })
                    .catch(() => {
                        this.isRequestError = true;
                    })
                    .finally(() => {
                        this.isLoading = false;
                    });
            },
            /**
             * @desc 
             */
            refresh: _.debounce(function () {
                this.fetchHostOfHost();
            }, 300),

            /**
             * @desc 
             */
            getAllHost () {
                return [
                    ...this.invalidList,
                    ...this.list,
                ];
            },

            /**
             * @desc 
             */
            removeAllInvalidHost () {
                this.invalidList = [];
                this.triggerChange();
            },
            /**
             * @desc 
             */
            getAllInvalidHost () {
                return this.invalidList;
            },

            triggerChange () {
                this.isInnerChange = true;
                this.$emit('on-change', [
                    ...this.invalidList,
                    ...this.list,
                ]);
            },
            /**
             * @desc 
             */
            handleRefresh () {
                this.fetchHostOfHost();
            },
            /**
             * @desc 
             * @prams host [Object] 
             */
            handleInvalidRemove (host) {
                const result = [];
                this.invalidList.forEach((currentHost) => {
                    if (host.realId !== currentHost.realId) {
                        result.push(currentHost);
                    }
                });
                this.invalidList = Object.freeze(result);
                this.triggerChange();
            },
            /**
             * @desc 
             */
            handleRemoveAll () {
                if (this.list.length < 1 && this.invalidList.length < 1) {
                    this.messageSuccess('');
                    return;
                }
                this.invalidList = [];
                this.list = [];
                this.messageSuccess('');
                this.triggerChange();
            },
            /**
             * @desc 
             * @param {String} hostRealId id
             */
            handleRemoveOne (hostRealId) {
                // 
                const result = [];
                this.list.forEach((currentHost) => {
                    if (currentHost.realId !== hostRealId) {
                        result.push(currentHost);
                    }
                });
                this.list = Object.freeze(result);
                this.triggerChange();
            },
            /**
             * @desc 
             */
            handleRemoveFail () {
                const effectiveIp = [];
                const failIp = [];
                this.list.forEach((currentHost) => {
                    if (currentHost.alive) {
                        effectiveIp.push(currentHost);
                    } else {
                        failIp.push(currentHost);
                    }
                });
                if (failIp.length < 1 && this.invalidList.length < 1) {
                    this.messageSuccess('');
                    return;
                }
                this.invalidList = [];
                this.list = Object.freeze(effectiveIp);
                this.messageSuccess('');
                this.triggerChange();
            },
        },
    };
</script>
