<template>
    <jb-collapse-item name="group">
        <!-- eslint-disable-next-line max-len -->
        <span class="panel-title">
            <span>{{ ''}}</span>
            <span class="strong number">{{ data.length }}</span>
            <span>{{ '' }}</span>
        </span>
        <action-extend v-if="editable">
            <div class="action-item" @click="handleRemoveAll">{{ '' }}</div>
        </action-extend>
        <template #content>
            <div v-bkloading="{ isLoading }">
                <host-table
                    v-if="!isRequestError"
                    :max-height="410"
                    :list="list"
                    :append-nums="invalidList.length">
                    <tbody
                        v-if="invalidList.length > 0"
                        class="invalid-list"
                        slot="appendBefore">
                        <tr
                            v-for="(row, index) in invalidList"
                            :key="`invalid_${index}`">
                            <td class="table-cell">
                                <span
                                    class="invalid"
                                    :tippy-tips="''">{{ '' }}</span>
                                <span>{{ row }}</span>
                            </td>
                            <td colspan="2">--</td>
                            <td v-if="editable" class="action-column">
                                <bk-button
                                    text
                                    @click="handleInvalidRemove(index)">
                                    {{ '' }}
                                </bk-button>
                            </td>
                        </tr>
                    </tbody>
                    <tbody class="valid-list">
                        <tr
                            v-for="(row, index) in list"
                            :key="index"
                            :class="diff[row.id]">
                            <td style="width: 40%; cursor: pointer;" @click="handleView(row.id)">
                                {{ row.name }}
                            </td>
                            <td style="width: 150px;">
                                <div class="cell-text"><span class="number strong">{{ row.total }}</span></div>
                            </td>
                            <td @click="handleView(row.id)">
                                <statistics-text style="cursor: pointer;" :data="row" />
                            </td>
                            <td v-if="editable" class="action-column">
                                <bk-button
                                    text
                                    @click="handleRemove(index)">
                                    {{ '' }}
                                </bk-button>
                            </td>
                        </tr>
                    </tbody>
                </host-table>
                <bk-exception v-if="isRequestError" type="500" style="padding-bottom: 50px;">
                    <div style="display: flex; font-size: 14px;">
                        <span></span>
                        <bk-button text @click="handleRefresh"></bk-button>
                    </div>
                </bk-exception>
            </div>
        </template>
    </jb-collapse-item>
</template>
<script>
    import _ from 'lodash';
    import AppService from '@service/app-manage';
       import JbCollapseItem from '@components/jb-collapse-item';
    import ActionExtend from '../components/action-extend';
    import HostTable from '../components/host-table';
    import StatisticsText from '../components/statistics-text';
    import {
        statisticsHost,
    } from '../components/utils';

    export default {
        name: 'ViewGroup',
        components: {
            JbCollapseItem,
            ActionExtend,
            HostTable,
            StatisticsText,
        },
        props: {
            data: {
                type: Array,
                required: true,
            },
            editable: {
                type: Boolean,
                default: false,
            },
            diff: {
                type: Object,
                default: () => ({}),
            },
        },
        data () {
            return {
                list: [{},{},{}],
                invalidList: [{},{},{}],
                isLoading: false,
                isRequestError: false,
            };
        },
        watch: {
            data: {
                handler (data) {
                    if (this.isInnerChange) {
                        this.isInnerChange = false;
                        return;
                    }
                    if (data.length < 1) {
                        this.list = [];
                        return;
                    }
                    this.fetchDynamicGroup();
                },
                immediate: true,
            },
        },
        created () {
            this.isInnerChange = false;
            // 
            this.groupMap = {};
        },
        methods: {
            /**
             * @desc 
             */
            fetchDynamicGroup () {
                this.isLoading = true;
                const allGroupMap = this.data.reduce((result, groupId) => {
                    result[groupId] = groupId;
                    return result;
                }, {});
                
                AppService.fetchHostOfDynamicGroup({
                    id: this.data.join(','),
                }).then((data) => {
                    const list = [];
                    this.groupMap = {};
                    data.forEach((item) => {
                        const { id, name, ipListStatus } = item;
                        const statisticsResult = statisticsHost(ipListStatus);

                        delete allGroupMap[id];

                        list.push({
                            id,
                            name,
                            ...statisticsResult,
                        });
                        this.groupMap[id] = {
                            id,
                            name,
                            ...statisticsResult,
                            host: Object.freeze(ipListStatus),
                        };
                    });
                    this.list = Object.freeze(list);
                    // allNodeMap
                    this.invalidList = Object.freeze(Object.values(allGroupMap));
                })
                    .catch(() => {
                        this.isRequestError = true;
                    })
                    .finally(() => {
                        this.isLoading = false;
                    });
            },
            /**
             * @desc 
             *
             */
            refresh: _.debounce(function () {
                this.fetchDynamicGroup();
            }, 300),
            /**
             * @desc 
             *
             */
            getAllHost () {
                const stack = [];
                for (const groupId in this.groupMap) {
                    stack.push(...this.groupMap[groupId].host);
                }
                return stack;
            },
            /**
             * @desc 
             */
            removeAllInvalidHost () {
                this.invalidList = [];
                this.triggerChange();
            },
            triggerChange () {
                this.isInnerChange = true;
                // id
                const groupIdList = [...this.invalidList];
                this.list.forEach((group) => {
                    groupIdList.push(group.id);
                });
                this.$emit('on-change', groupIdList);
            },
            /**
             * @desc 
             */
            handleRefresh () {
                this.fetchDynamicGroup();
            },
            /**
             * @desc 
             * @param {Number} index 
             */
            handleInvalidRemove (index) {
                const invalidList = [
                    ...this.invalidList,
                ];
                invalidList.splice(index, 1);
                this.invalidList = Object.freeze(invalidList);
                this.triggerChange();
            },
            /**
             * @desc 
             *
             */
            handleRemoveAll () {
                if (this.data.length < 1) {
                    this.messageSuccess('');
                    return;
                }
                this.list = [];
                this.groupMap = {};
                this.invalidList = [];
                this.triggerChange();
                this.messageSuccess('');
            },
            /**
             * @desc 
             * @param {Number} index 
             */
            handleRemove (index) {
                const currentNode = this.list[index];
                const list = [...this.list];
                list.splice(index, 1);
                this.list = Object.freeze(list);
                delete this.groupMap[currentNode.id];
                this.triggerChange();
                this.messageSuccess('');
            },
            /**
             * @desc 
             * @param {String} id id
             */
            handleView (id) {
                this.$emit('on-view', Object.freeze(this.groupMap[id]));
            },
        },
    };
</script>
